body {
	font-family: 'Poppins', sans-serif;
	font-size:10px;
}

$white: #FFFFFF; 
$grey: #9DA8AD;
$brown: #525250;
$light: #CECFCE;
$yellow:rgb(255, 166, 0)
;

// ====================================
// Global Classes / Wordpress Specific
// ====================================
// */

.aligncenter {
	text-align: center;
	margin: 0 auto;
}

.alignleft {
	float: left;
}

.alignright {
	float: right;
}

img {
	&.alignleft {
		padding-right: 5px;
	}
	&.alignright {
		padding-left: 5px;
	}
}

.clear {
	width: 100%;
	float: left;
	clear: both;
}